body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 80px;
}

.App-header {
  /* background-color: #222; */
  background-color: darkgray;
  height: 150px;
  padding: 20px;
  color: white;
  margin-bottom: 20px;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.order-book {
  /* padding-top: 10em; */
  overflow-y: scroll;
  max-height: 100em;
  display: inline-block;
  width: 35vw;
}

.dex-window {
  border-style: outset;
}

.orders-list {
  border-style: inset;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.order-display {
  display: block;
  text-align: right;
  padding-bottom: 10px;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}
.text-muted {
  font-weight: bolder;
}

.dex-main {
  /* background-color: #343a40;
  box-shadow: 0 0.5rem 1rem rgb(0 0 0 / 15%) !important; */
}

.action-toolbar {
  /* padding-top: 1rem; */
}

.button-selected {
  border: 2px solid rgb(0, 0, 255);
}

.form-toggle-buttons {
  padding-top: 10px;
}

.order-input {
  width: 50%;
}

